import React, { useState } from "react";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import IconButton from "@material-ui/core/IconButton";
import TranslateOutlinedIcon from "@material-ui/icons/TranslateOutlined";

const languages = [
  { shortcut: "jp", translationKey: "language.japanese" },
  { shortcut: "en", translationKey: "language.english" },
  { shortcut: "pl", translationKey: "language.polish" }
];

export default function LanguageSelector(props) {
  const { t, onLanguageChange } = props;
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = language => {
    if (language) {
      onLanguageChange(language);
    }
    setAnchorEl(null);
  };

  return (
    <div>
      <IconButton onClick={handleClick} color="inherit">
        <TranslateOutlinedIcon />
      </IconButton>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={() => handleClose()}
      >
        {languages.map(l => (
          <MenuItem key={l.shortcut} onClick={() => handleClose(l.shortcut)}>
            {t(l.translationKey)}
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
}
