import React, { useState, Fragment } from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import {
  ShowChart,
  AssignmentOutlined,
  AttachMoney,
  AccountBalanceOutlined,
  SettingsOutlined,
  ExpandLess,
  ExpandMore
} from "@material-ui/icons";
import { useTranslation } from "react-i18next";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import ListItemLink from "./ListItemLink";

const useStyles = makeStyles(theme => ({
  nested: {
    paddingLeft: theme.spacing(4)
  }
}));

const menuStructure = iconColor => {
  var styles = { color: "black" };

  return {
    items: [
      {
        key: "dashboard",
        scope: "read:dashboard",
        path: "/dashboard",
        translationKey: "menu.dash",
        icon: <ShowChart style={styles} />
      },
      {
        key: "transactions",
        scope: "read:transactions",
        path: "/transactions",
        translationKey: "menu.transactions",
        icon: <AttachMoney style={styles} />
      },
      {
        key: "settings",
        translationKey: "menu.settings",
        icon: <SettingsOutlined style={styles} />,
        items: [
          {
            key: "projects",
            scope: "read:projects",
            path: "/projects",
            translationKey: "menu.projects",
            icon: <AssignmentOutlined style={styles} />
          },
          {
            key: "transaction-categories",
            scope: "read:transaction-categories",
            path: "/transaction-categories",
            translationKey: "menu.transaction-categories",
            icon: <AccountBalanceOutlined style={styles} />
          }
        ]
      }
    ]
  };
};

const mapMenu = (scopes, menuList) => {
  return menuList
    .map(item => {
      if (item.scope) {
        return scopes.includes(item.scope) ? item : null;
      }
      if (!item.items) {
        return null;
      }
      let mappedItems = mapMenu(scopes, item.items);
      if (!mappedItems || mappedItems.length === 0) {
        return null;
      }
      return { ...item, items: mappedItems };
    })
    .filter(item => item !== null);
};

export default function SidebarMenu(props) {
  const { scopes } = props;
  const { t } = useTranslation();
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = useState({});
  const handleClick = key => {
    let newOpen = { ...open };
    newOpen[key] = !open[key];
    setOpen(newOpen);
  };

  const renderItems = (items, isNested) => {
    let nestedClassName = isNested ? classes.nested : null;

    return items.map(item => {
      if (item.path) {
        return (
          <ListItemLink
            key={item.key}
            to={item.path}
            primary={t(item.translationKey)}
            icon={item.icon}
            className={nestedClassName}
          />
        );
      }

      return (
        <Fragment key={item.key}>
          <ListItem button onClick={() => handleClick(item.key)}>
            <ListItemIcon>{item.icon}</ListItemIcon>
            <ListItemText primary={t(item.translationKey)} />
            {open[item.key] ? <ExpandLess /> : <ExpandMore />}
          </ListItem>

          <Collapse in={open[item.key]} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {renderItems(item.items, true)}
            </List>
          </Collapse>
        </Fragment>
      );
    });
  };

  let menuItems = mapMenu(
    scopes,
    menuStructure(theme.palette.primary.contrastText).items
  );

  return <List>{renderItems(menuItems, false)}</List>;
}
