import React from "react";
import { ThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import history from "./utils/history";
import { Router } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import RootLayout from "./components/layout/RootLayout";
import purple from "@material-ui/core/colors/purple";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import CustomDateFnsUtils from "./utils/datePicker/CustomDateFnsUtils";
import useLocaleUtils from "./utils/localeUtils";

toast.configure({
  position: toast.POSITION.TOP_RIGHT,
  autoClose: false,
  newestOnTop: true,
  closeOnClick: true,
  rtl: false,
  pauseOnVisibilityChange: true,
  draggable: false,
  className: "customToastContainer"
});

const mainTheme = createMuiTheme({
  palette: {
    primary: {
      main: "#005d32"
    },
    secondary: {
      main: purple[500]
    }
    //secondary: {"#005d32"
    //  main: "#23a313"#014e2a, #005d32
    //}
    // "#9ca473" #cad984
  },
  typography: {
    // Use the system font.
    fontFamily:
      " Pro W3, Hiragino Kaku Gothic Pro, Osaka, , Meiryo,  , MS PGothic, sans-serif"
  }
});

function App() {
  const localeUtils = useLocaleUtils();

  return (
    <Router history={history}>
      <ThemeProvider theme={mainTheme}>
        <MuiPickersUtilsProvider
          utils={CustomDateFnsUtils}
          locale={localeUtils.getCurrentLocale()}
        >
          <RootLayout />
        </MuiPickersUtilsProvider>
      </ThemeProvider>
    </Router>
  );
}

export default App;
