import format from "date-fns/format";
import formatISO from "date-fns/formatISO";
import parseISO from "date-fns/parseISO";
import enLocale from "date-fns/locale/en-GB";
import plLocale from "date-fns/locale/pl";
import jpLocale from "./datePicker/datePickerJpLocale";
import { useTranslation } from "react-i18next";

const localeMap = { pl: plLocale, en: enLocale, jp: jpLocale };

const getLocale = (languageKey) => localeMap[languageKey] || enLocale;

export default function useLocaleUtils() {
  const { i18n } = useTranslation();

  return {
    renderShortDate: (date) =>
      date && format(parseISO(date), "P", { locale: getLocale(i18n.language) }),

    getDateString: (date) => {
      var d = date instanceof Date ? date : parseISO(date);
      return d && formatISO(d, { representation: "date" });
    },

    getCurrentLocale: () => getLocale(i18n.language),
  };
}
