import { notifyError } from "./notify.js";
import { useAuth0 } from "../react-auth0-spa";
import { useTranslation } from "react-i18next";
import config from "../config";

const API_URL = config.API_URL;
const API_TIMEOUT_S = config.API_TIMEOUT_S;

const DO_NOTHING_CLIENT = {
  get: (url) => Promise.resolve({}),
  delete: (url) => Promise.resolve({}),
  post: (url, body) => Promise.resolve({}),
};

const withTimeout = (url, options) => {
  var controller = new AbortController();
  var signal = controller.signal;

  setTimeout(() => controller.abort(), API_TIMEOUT_S * 1000);
  return fetch(url, {
    signal: signal,
    mode: "cors",
    ...options,
  });
};

export default function useApiClient() {
  const { loading, isAuthenticated, getTokenSilently, logout } = useAuth0();
  const { t } = useTranslation();

  const handleError = (error) => {
    console.log(error);
    if (error.status && error.status === 403) {
      logout();
    } else {
      var errorMessage = error.status
        ? t("api.error.status", { status: error.status })
        : t("api.error");
      notifyError(errorMessage);
      return Promise.reject(error);
    }
  };

  const headers = (token, containsBody = false) => {
    var headers = {
      Accept: "application/json",
      Authorization: `Bearer ${token}`,
    };
    if (containsBody) {
      headers["Content-Type"] = "application/json";
    }
    return headers;
  };

  const get = (url) =>
    getTokenSilently()
      .then((token) =>
        withTimeout(API_URL + url, {
          method: "GET",
          headers: headers(token),
        })
      )
      .then((res) => (res.ok ? res.json() : Promise.reject(res)))
      .catch(handleError);

  const del = (url, id) =>
    getTokenSilently()
      .then((token) =>
        withTimeout(`${API_URL}${url}/${id}`, {
          method: "DELETE",
          headers: headers(token),
        })
      )
      .then((res) => (res.ok ? true : Promise.reject(res)))
      .catch(handleError);

  const post = (url, content) =>
    getTokenSilently()
      .then((token) =>
        withTimeout(API_URL + url, {
          method: "POST",
          headers: headers(token, true),
          body: JSON.stringify(content),
        })
      )
      .then((res) => (res.ok ? res.json() : Promise.reject(res)))
      .catch(handleError);

  const put = (url, id, content) =>
    getTokenSilently()
      .then((token) =>
        withTimeout(`${API_URL}${url}/${id}`, {
          method: "PUT",
          headers: headers(token, true),
          body: JSON.stringify(content),
        })
      )
      .then((res) => (res.ok ? res.json() : Promise.reject(res)))
      .catch(handleError);

  if (loading || !isAuthenticated) {
    return DO_NOTHING_CLIENT;
  }

  return { get: get, delete: del, post: post, put: put };
}
