import React from "react";
import { useFormik } from "formik";
import { useTranslation } from "react-i18next";
import * as Yup from "yup";
import DialogComponent from "../DialogComponent";
import EditProjectPanel from "./EditProjectPanel";
import useLocaleUtils from "../../utils/localeUtils";

const createProject = (values, formatDate, version) => {
  console.log(
    `creating project, got values: ${JSON.stringify(values)} and start date: ${
      values.startDate
    }`
  );

  var newProject = {
    name: values.name,
    projectCode: values.code,
    contract: values.contract,
    note: values.note,
    startDate: values.startDate && formatDate(values.startDate),
    endDate: values.endDate && formatDate(values.endDate),
    paymentDate: values.paymentDate && formatDate(values.paymentDate),
    version: version || null
  };
  if (values.includeContactInformation) {
    newProject.contactInformation = {
      name: values.contactName,
      email: values.contactEmail,
      phone: values.contactPhone
    };

    if (values.addressLine) {
      newProject.contactInformation.address = {
        addressLine: values.addressLine,
        postalCode: values.postalCode
      };
    }
  }
  return newProject;
};

export default function EditProjectControl(props) {
  const { isEditing, onProjectSaved, onCanceled, project } = props;
  const { t } = useTranslation();
  const localeUtils = useLocaleUtils();

  const contactInfo = project.contactInformation || {};
  const address = contactInfo.address || {};

  const validationSchema = Yup.object({
    name: Yup.string().required(t("form.validation.required")),
    contactEmail: Yup.string().email(t("form.validation.invalid-email")),
    contactName: Yup.string().when(
      "includeContactInformation",
      (include, schema) =>
        include ? schema.required(t("form.validation.required")) : schema
    ),
    code: Yup.number()
      .min(0, t("form.validation.non-negative"))
      .integer(t("form.validation.integer")),
    contract: Yup.boolean(),
    includeContactInformation: Yup.boolean(),
    startDate: Yup.date().nullable(),
    endDate: Yup.date().nullable(),
    paymentDate: Yup.date().nullable()
  });

  const handleSave = (values, callbacks) =>
    onProjectSaved(
      createProject(values, localeUtils.getDateString, project.version),
      project.id,
      () => {
        callbacks.setSubmitting(false);
        callbacks.resetForm();
      }
    );

  const formik = useFormik({
    initialValues: {
      name: project.name,
      code: project.projectCode,
      contract: project.contract || false,
      note: project.note,
      startDate: project.startDate || null,
      endDate: project.endDate || null,
      paymentDate: project.paymentDate || null,
      includeContactInformation:
        typeof project.contactInformation !== "undefined",
      contactName: contactInfo.name,
      contactPhone: contactInfo.phone,
      contactEmail: contactInfo.email,
      addressLine: address.addressLine,
      postalCode: address.postalCode
    },
    validationSchema,
    onSubmit: handleSave,
    enableReinitialize: true
  });

  if (!isEditing) {
    return null;
  }

  const handleCancel = () => {
    formik.handleReset();
    onCanceled();
  };

  const title = project.id
    ? t("projects.editing", { projectName: project.name })
    : t("projects.new");

  return (
    <DialogComponent
      open={isEditing}
      title={title}
      handleSave={formik.handleSubmit}
      handleCancel={handleCancel}
      isSubmitting={formik.isSubmitting}
    >
      <EditProjectPanel formik={formik} />
    </DialogComponent>
  );
}
