import React from "react";
import Layout, { Root } from "@mui-treasury/layout";
import HeaderLayout from "./HeaderLayout";
import SidebarLayout from "./SidebarLayout";
import ContentLayout from "./ContentLayout";
import CssBaseline from "@material-ui/core/CssBaseline";

const scheme = Layout();

scheme.configureHeader((builder) => {
  builder
    .registerConfig("xs", {
      position: "sticky",
    })
    .registerConfig("md", {
      position: "relative",
    });
});

scheme.configureEdgeSidebar((builder) => {
  builder
    .create("sidebar", { anchor: "left" })
    .registerTemporaryConfig("xs", {
      anchor: "left",
      width: "auto", // 'auto' is only valid for temporary variant
    })
    .registerPersistentConfig("md", {
      width: 256,
      headerMagnetEnabled: true,
      persistentBehavior: "fit",
    });
});

export default function RootLayout() {
  const sidebarId = "sidebar";
  return (
    <Root scheme={scheme} themeProviderOmitted={true}>
      <CssBaseline />
      <HeaderLayout sidebarId={sidebarId} />
      <SidebarLayout sidebarId={sidebarId} />
      <ContentLayout />
    </Root>
  );
}
