import React from "react";
import { Alert, AlertTitle } from "@material-ui/lab";
import { useTranslation } from "react-i18next";

const errorCodes = { auth: "error.auth" };
const defaultMessageKey = "error.default";

const readParams = searchString =>
  searchString
    .substring(1)
    .split("&")
    .map(part => part.split("="))
    .reduce((map, value) => {
      if (value.length > 1) {
        map[value[0]] = value[1];
      }
      return map;
    }, {});

export default function ErrorDisplayComponent(props) {
  const { t } = useTranslation();

  let searchString = props.location.search;
  let params = (searchString && readParams(searchString)) || {};

  let errorCode = params["errorCode"];
  let errorMessageKey =
    (errorCode && errorCodes[errorCode]) || defaultMessageKey;
  let message = t(errorMessageKey);

  return (
    <Alert severity="error">
      <AlertTitle>{t("error.title")}</AlertTitle>
      {message}
    </Alert>
  );
}
