import React from "react";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import useStyles from "./projectStyles";
import PanelFragment from "./PanelFragment";
import { useTranslation } from "react-i18next";

export default function ProjectDetailPanel(props) {
  const classes = useStyles();
  const { t } = useTranslation();

  const { contactInformation, note } = props;

  const item = (title, values, wrap = true) => {
    let result = (
      <>
        <Typography variabt="subtitle1" className={classes.padded}>
          {title}:
        </Typography>
        {values.map((value, index) => (
          <Typography
            key={index}
            variant="body2"
            className={classes.halfPadded}
          >
            {value}
          </Typography>
        ))}
        <div className={classes.bottomPadded} />
      </>
    );

    return wrap ? (
      <Grid item xs={6} sm={4}>
        {result}
      </Grid>
    ) : (
      result
    );
  };

  return (
    <div className={classes.root}>
      <Grid container spacing={3} alignItems="flex-start">
        <Grid item sm={12} xs={12} md={6}>
          {note && (
            <PanelFragment title={t("projects.notes")}>
              <Typography variant="body2" className={classes.padded}>
                {note}
              </Typography>
            </PanelFragment>
          )}
        </Grid>

        <Grid item sm={12} xs={12} md={6}>
          {contactInformation && (
            <PanelFragment title={t("projects.contact-information")}>
              <Grid container>
                {item(t("projects.contact.name"), [contactInformation.name])}
                {item(t("projects.contact.phone"), [contactInformation.phone])}
                {item(t("projects.contact.email"), [contactInformation.email])}
              </Grid>
              {contactInformation.address &&
                item(
                  t("projects.contact.address"),
                  [
                    contactInformation.address.postalCode,
                    contactInformation.address.addressLine
                  ],
                  false
                )}
            </PanelFragment>
          )}
        </Grid>
      </Grid>
    </div>
  );
}
