import React, { createRef, useState } from "react";
import MaterialTable from "material-table";
import { useTranslation } from "react-i18next";
import ProjectDetailPanel from "./ProjectDetailPanel";
import useApiClient from "../../utils/apiClient";
import LoadingComponent from "../LoadingComponent";
import EditProjectControl from "./EditProjectControl";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import SyncIcon from "@material-ui/icons/Sync";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import projectTableSettings from "./projectTableSettings";
import { notifySuccess } from "../../utils/notify.js";
import useLocaleUtils from "../../utils/localeUtils";

const detailPanel = (row) => (
  <ProjectDetailPanel
    note={row.note}
    contactInformation={row.contactInformation}
  />
);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  title: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  button: {
    marginBottom: theme.spacing(2),
  },
  deleteIcon: {
    color: theme.palette.error.main,
  },
}));

export default function Projects() {
  const { t } = useTranslation();
  const apiClient = useApiClient();
  const tableRef = createRef();
  const refresh = () => tableRef.current && tableRef.current.onQueryChange();
  const [editing, setEditing] = useState(false);
  const [currentProject, setCurrentProject] = useState({});
  const styles = useStyles();
  const localeUtils = useLocaleUtils();

  const initEdit = (project) => {
    setCurrentProject(project);
    setEditing(true);
  };

  const closeEdit = () => {
    setEditing(false);
    setCurrentProject({});
  };

  const getData = (query) => {
    console.log(`got query: ${JSON.stringify(query)}`);
    return apiClient
      .get("projects")
      .then((json) =>
        Promise.resolve({
          data: json.projects,
          page: 0,
          totalCount: json.projects.length,
        })
      )
      .catch((error) => Promise.resolve(projectTableSettings.emptyData));
  };

  const actions = [
    {
      icon: () => <EditIcon color="primary" />,
      tooltip: t("action.edit"),
      onClick: (event, row) => {
        console.log(`editing ${JSON.stringify(row)}`);
        initEdit(row);
      },
    },
    {
      icon: () => <SyncIcon color="primary" fontSize="large" />,
      tooltip: "Refresh Data",
      isFreeAction: true,
      onClick: refresh,
    },
  ];

  const editable = {
    onRowDelete: (row) => {
      console.log(`deleting ${JSON.stringify(row)}`);
      return apiClient
        .delete("projects", row.id)
        .then(() =>
          notifySuccess(t("projects.deleted", { projectName: row.name }))
        )
        .catch((error) => Promise.resolve(projectTableSettings.emptyData));
    },
  };

  const icons = {
    Delete: () => <DeleteOutlineIcon className={styles.deleteIcon} />,
  };

  const createOrUpdate = (project, id) =>
    id
      ? () =>
          apiClient
            .put("projects", id, project)
            .then(() =>
              notifySuccess(t("projects.edited", { projectName: project.name }))
            )
      : () =>
          apiClient
            .post("projects", project)
            .then(() =>
              notifySuccess(
                t("projects.created", { projectName: project.name })
              )
            );

  const handleEditPanelClose = (newProject, id, onSubmitComplete) => {
    console.log(`yay :D got new project: ${JSON.stringify(newProject)}`);
    createOrUpdate(newProject, id)()
      .then((_) => refresh())
      .finally(() => {
        onSubmitComplete();
        closeEdit();
      });
  };

  return (
    <LoadingComponent>
      <Typography variant="h5">{t("projects.projects")}</Typography>

      <div className={styles.root}>
        <Grid container direction="row" justify="flex-end" alignItems="center">
          <Button
            variant="contained"
            color="primary"
            startIcon={<AddIcon />}
            className={styles.button}
            disableElevation
            onClick={() => initEdit({})}
          >
            {t("action.add")}
          </Button>
        </Grid>
      </div>

      <EditProjectControl
        isEditing={editing}
        onProjectSaved={handleEditPanelClose}
        onCanceled={closeEdit}
        project={currentProject}
      />

      <MaterialTable
        columns={projectTableSettings.columns(t, localeUtils.renderShortDate)}
        data={getData}
        options={projectTableSettings.options}
        detailPanel={detailPanel}
        onRowClick={(event, rowData, togglePanel) => togglePanel()}
        editable={editable}
        actions={actions}
        tableRef={tableRef}
        localization={projectTableSettings.localization(t)}
        icons={icons}
      />
    </LoadingComponent>
  );
}
