import buildLocalizeFn from "./buildLocalizeFn";

const monthValues = {
  wide: [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    ""
  ]
};

const shortDays = ["", "", "", "", "", "", ""];

const dayValues = {
  narrow: shortDays,
  short: shortDays,
  abbreviated: shortDays,
  wide: ["", "", "", "", "", "", ""]
};

const eraValues = {
  narrow: ["BC", "AD"],
  abbreviated: ["BC", "AD"],
  wide: ["", ""]
};

const quarterValues = {
  narrow: ["1", "2", "3", "4"],
  abbreviated: ["Q1", "Q2", "Q3", "Q4"],
  wide: ["1st quarter", "2nd quarter", "3rd quarter", "4th quarter"]
};

const dayPeriods = {
  am: "",
  pm: "",
  midnight: "",
  noon: "",
  morning: "",
  afternoon: "",
  evening: "",
  night: ""
};

const dayPeriodValues = {
  narrow: dayPeriods,
  abbreviated: dayPeriods,
  wide: dayPeriods
};

const localize = {
  ordinalNumber: (dirtyNumber, dirtyOptions) => dirtyNumber + "",
  month: buildLocalizeFn({
    values: monthValues,
    defaultWidth: "wide"
  }),
  day: buildLocalizeFn({
    values: dayValues,
    defaultWidth: "wide"
  }),
  era: buildLocalizeFn({
    values: eraValues,
    defaultWidth: "wide"
  }),
  quarter: buildLocalizeFn({
    values: quarterValues,
    defaultWidth: "wide",
    argumentCallback: quarter => Number(quarter) - 1
  }),
  dayPeriod: buildLocalizeFn({
    values: dayPeriodValues,
    defaultWidth: "wide"
  })
};

export default localize;
