import React from "react";
import TextField from "@material-ui/core/TextField";
import Switch from "@material-ui/core/Switch";
import Checkbox from "@material-ui/core/Checkbox";
import Grid from "@material-ui/core/Grid";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import { useTranslation } from "react-i18next";
import Box from "@material-ui/core/Box";
import { KeyboardDatePicker } from "@material-ui/pickers";

export default function EditProjectPanel(props) {
  const formik = props.formik;
  const { t } = useTranslation();

  const errorProps = key =>
    formik.touched[key] && formik.errors[key]
      ? { error: true, helperText: formik.errors[key] }
      : {};

  const fieldProps = (key, translationKey) => ({
    ...formik.getFieldProps(key),
    ...errorProps(key),
    size: "small",
    variant: "outlined",
    id: key,
    fullWidth: true,
    label: t(translationKey)
  });

  const dateField = (key, translationKey) => (
    <KeyboardDatePicker
      autoOk
      id={key}
      label={t(translationKey)}
      value={formik.values[key]}
      onChange={value => {
        console.log(`got date: ${value}`);
        formik.setFieldValue(key, value, false);
      }}
      onBlur={formik.handleBlur}
      format="yyyy/MM/dd"
      variant="inline"
      inputVariant="outlined"
      invalidDateMessage={t("form.validation.invalid-date-format")}
    />
  );

  return (
    <form onSubmit={formik.handleSubmit}>
      <Grid container spacing={2} alignItems="flex-start" justify="flex-start">
        <Grid item xs={12}>
          <Typography variant="subtitle1">
            {t("projects.basic-information")}
          </Typography>
          <Divider />
        </Grid>

        <Grid item xs={12}>
          <TextField {...fieldProps("name", "projects.name")}></TextField>
        </Grid>

        <Grid item xs={12} md={6}>
          <TextField
            {...fieldProps("code", "projects.code")}
            type="number"
          ></TextField>
        </Grid>

        <Grid item xs={12} md={6}>
          <FormControlLabel
            control={
              <Switch
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                checked={formik.values.contract}
                name="contract"
                color="primary"
              />
            }
            label={t("projects.contract")}
          />
        </Grid>

        <Grid item xs={12} md={6}>
          {dateField("startDate", "projects.start-date")}
        </Grid>

        <Grid item xs={12} md={6}>
          {dateField("endDate", "projects.end-date")}
        </Grid>

        <Grid item xs={12} md={6}>
          {dateField("paymentDate", "projects.payment-date")}
        </Grid>

        <Grid item xs={12}>
          <TextField
            {...fieldProps("note", "projects.notes")}
            multiline
            rows={2}
          />
        </Grid>

        <Grid item xs={12}>
          <FormControlLabel
            control={
              <Checkbox
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                checked={formik.values.includeContactInformation}
                name="includeContactInformation"
                color="primary"
              />
            }
            label={t("projects.include-contact")}
          />
        </Grid>
      </Grid>

      <Box display={formik.values.includeContactInformation ? "block" : "none"}>
        <Grid
          container
          spacing={2}
          alignItems="flex-start"
          justify="flex-start"
        >
          <Grid item xs={12}>
            <Typography variant="subtitle1">
              {t("projects.contact-information")}
            </Typography>
            <Divider />
          </Grid>

          <Grid
            item
            xs={12}
            visibility={
              formik.values.includeContactInformation ? "visible" : "hidden"
            }
          >
            <TextField
              {...fieldProps("contactName", "projects.contact.name")}
            ></TextField>
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              {...fieldProps("contactPhone", "projects.contact.phone")}
            ></TextField>
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              {...fieldProps("contactEmail", "projects.contact.email")}
            ></TextField>
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              {...fieldProps(
                "postalCode",
                "projects.contact.address.postal-code"
              )}
            ></TextField>
          </Grid>

          <Grid item xs={12}>
            <TextField
              {...fieldProps("addressLine", "projects.contact.address-line")}
            ></TextField>
          </Grid>
        </Grid>
      </Box>
    </form>
  );
}
