import buildFormatLongFn from "./buildFormatLongFn";

const dateFormats = {
  full: "yyyy''MM''dd''(EEEE)",
  long: "yyyy''MM''dd''",
  medium: "yyyy''MM''dd''",
  short: "yyyy/MM/dd"
};

const timeFormats = {
  full: "HH:mm:ss",
  long: "HH:mm:ss",
  medium: "HH:mm:ss",
  short: "HH:mm"
};

const dateTimeFormats = {
  full: "{{date}} {{time}}"
};

const formatLong = {
  date: buildFormatLongFn({
    formats: dateFormats,
    defaultWidth: "full"
  }),
  time: buildFormatLongFn({
    formats: timeFormats,
    defaultWidth: "full"
  }),
  dateTime: buildFormatLongFn({
    formats: dateTimeFormats,
    defaultWidth: "full"
  })
};

export default formatLong;
